.topbar {
  width: 100%;
  height: 50px;
  /* position: sticky; */
  /* top: 0; */
  /* z-index: 10; */
  /* background: aqua; */
}
.topbarWrapper {
  height: 100%;
  padding: 0px 20px;
  display: flex;
  /* background: white; */
  align-items: center;
  justify-content: space-between;
}
.logo {
  font-weight: bold;
  font-size: 30px;
  color: darkblue;
  cursor: pointer;
}
.topRight {
  display: flex;
  align-items: center;
}
.topbarIconContainer {
  position: relative;
  background: white;
  cursor: pointer;
  margin-right: 10px;
  color: #555;
}
.topIconBadge {
  position: absolute;
  width: 15px;
  height: 15px;
  top: -5px;
  right: -5px;
  background-color: red;
  color: white;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 10px;
}
.topAvatar {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  cursor: pointer;
}
